@font-face {
  font-family: "MyUnderwood";
  src: local("MyUnderwood.ttf") url(MyUnderwood.ttf) format('ttf') );
}

body{
  color: #2A2A57;
  font-family: 'Special Elite', cursive;
  background:url(https://i.imgur.com/HtUcuP0.png);
}

textarea{
  color: #2A2A57;
  font-family: 'Special Elite', cursive;
  font-size: 24px;
  padding: 5px;
  width:300px;
  height:48px;
  line-height: 27px;
}

button {
  color: white;
  font-family: 'Special Elite', cursive;
  width: 315px;
  height:50px;
  font-size:24px;
  margin-top:10px;
  margin-top:50px;
  background: #2A2A57;
}

.home {
  margin-left: 30px;
  position: relative;
  margin-top: 30px;
}

.poems {
  -webkit-transition: opacity 3s;
  -o-transition: opacity 3s;
  transition: opacity 3s;
  position: absolute;
  top:0;
  font-size:34px;
  margin-top:50px
}

.answers {
  -webkit-transition: opacity 3s;
  -o-transition: opacity 3s;
  transition: opacity 3s;
  position: absolute;
  top:0
}

h3{
  margin-top:50px;
  margin-bottom:20px;
  font-size:30px;
}



